import React, { useEffect } from 'react'
import Product from './Product';
import { getProduct } from '../actions/ProductAction';
import { useDispatch } from 'react-redux';

const product = {
    name: "Ring1",
    image: [{ url: "Images/i4.JPG" }],
    price: "$300",
    _id: "ring"
}

const Earring = () => {
    const dispatch = useDispatch();

    useEffect(()=>{
        dispatch(getProduct());
    },[dispatch]);

    return (
        <div>
            <div className="container-fluid">
                <div className="row bg-light b">
                    <div className="col-lg-4 py-3 ps-4">
                        <img className='vcom rounded-5' src="Images/v3.gif" alt='one' />
                    </div>
                    <div className="col-lg-8 pt-4">
                        <h1 className='text-primary text-center pt-5'>Rings</h1>
                    </div>
                </div>
            </div>

            <div className="container">

                <div className="row my-5">

                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                    <div className="col-lg-4 my-4">
                        <Product product={product} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Earring